import React from "react";
import PropTypes from "prop-types";

const OrderInfo = ({
    name,
    email,
    phone,
    city,
    street,
    houseNumber,
    flatNumber,
    comment,
    cash,
    costOrder,
    discountPromo
}) => {
    return (
        <div className="row">
            <div className="col-12">
                <div className="separation_cart-btn" />
            </div>
            <div className="col-12 col-xl-6 ">
                <ul>
                    <li className="mt-2">1.   - {name}</li>
                    <li className="mt-2">2. Email - {email}</li>
                    <li className="mt-2">3.   - {phone}</li>
                    <li className="mt-2">4.  - {city}</li>
                    <li className="mt-2">5.  - {street}</li>
                    <li className="mt-2">6.   - {houseNumber}</li>
                    <li className="mt-2">7.   - {flatNumber}</li>
                    <li className="mt-2">
                        8.   {cash ? "-" : "-"}
                    </li>
                    <p className="mt-2">9.  - {comment}</p>
                </ul>
            </div>
            <div className="col-12 col-xl-6">
                <div className="total">
                    <p className="total__subtotal d-block">
                        :{" "}
                        <span className="total__subtotal_size">
                            ${costOrder}
                        </span>
                    </p>
                    <p className="total__subtotal d-block">
                        :{" "}
                        <span className="total__subtotal_size">
                            ${discountPromo}
                        </span>
                    </p>
                    <p className="d-block total__text">
                        :
                        <span className="total__size">
                            ${costOrder - discountPromo}
                        </span>
                    </p>
                </div>
            </div>
        </div>
    );
};
OrderInfo.propTypes = {
    name: PropTypes.string.isRequired,
    email: PropTypes.string.isRequired,
    phone: PropTypes.string.isRequired,
    city: PropTypes.string.isRequired,
    street: PropTypes.string.isRequired,
    houseNumber: PropTypes.string.isRequired,
    flatNumber: PropTypes.string.isRequired,
    comment: PropTypes.string.isRequired,
    cash: PropTypes.bool.isRequired,
    costOrder: PropTypes.number.isRequired,
    discountPromo: PropTypes.number.isRequired
};
export default OrderInfo;
