import React, { useState, useEffect } from "react";
import { validator } from "../../utils/validator";
import TextField from "../common/form/textField";
import SelectField from "../common/form/selectField";
import CheckBoxField from "../common/form/checkBoxField";
import { useDispatch, useSelector } from "react-redux";
import { signUp, getAuthError } from "../../store/users";
import { getCities } from "../../store/cities";

const RegistorForm = () => {
    const dispatch = useDispatch();
    const [data, setData] = useState({
        email: "",
        password: "",
        name: "",
        city: "",
        licence: false,
        admin: false
    });
    const registorError = useSelector(getAuthError());
    const [errors, setErrors] = useState({});
    const cities = useSelector(getCities());
    const citiesList = cities.map((citiesName) => ({
        label: citiesName.name,
        value: citiesName._id
    }));

    const handleChange = (target) => {
        setData((prevState) => ({
            ...prevState,
            [target.name]: target.value
        }));
    };
    const validatorConfig = {
        email: {
            isRequired: {
                message: "    "
            },
            isEmail: {
                message: "Email  "
            }
        },
        name: {
            isRequired: {
                message: "   "
            },
            min: {
                message: "     3 ",
                valeo: 3
            }
        },
        password: {
            isRequired: {
                message: "   "
            },
            isCapitalSymbol: {
                message: "       "
            },
            isContainDigit: {
                message: "      "
            },
            min: {
                message: "     8 ",
                valeo: 8
            }
        },
        city: {
            isRequired: {
                message: "   "
            }
        },
        licence: {
            isRequired: {
                message:
                    "         "
            }
        }
    };
    useEffect(() => {
        validate();
    }, [data]);
    const validate = () => {
        const errors = validator(data, validatorConfig);
        setErrors(errors);
        return Object.keys(errors).length === 0;
    };
    const isValid = Object.keys(errors).length === 0;

    const handleSubmit = (e) => {
        e.preventDefault();
        const isValid = validate();
        if (!isValid) return;
        const newData = {
            ...data
        };
        dispatch(signUp(newData));
    };
    return (
        <form onSubmit={handleSubmit}>
            <TextField
                label=" "
                name="email"
                value={data.email}
                onChange={handleChange}
                error={errors.email}
            />
            <TextField
                label=""
                name="name"
                value={data.name}
                onChange={handleChange}
                error={errors.name}
            />
            <TextField
                label=""
                type="password"
                name="password"
                value={data.password}
                onChange={handleChange}
                error={errors.password}
            />
            <SelectField
                onChange={handleChange}
                options={citiesList}
                name="city"
                label="  "
                defaultOption=""
                error={errors.city}
                value={data.city}
                css={["mt-3 mb-3", null]}
            />
            <CheckBoxField
                value={data.licence}
                onChange={handleChange}
                name="licence"
                error={errors.licence}
            >
                 <a> </a>
            </CheckBoxField>
            {registorError && <p className="text-danger">{registorError}</p>}
            <button
                type="submit"
                disabled={!isValid}
                className="form__btn btn-pr btn-pr_bg"
            >
                
            </button>
        </form>
    );
};

export default RegistorForm;
